import {
    Select,
    HStack,
    TagCloseButton,
    TagLabel,
    Tag,
    Divider,
    Heading,
} from "@chakra-ui/react";

type FilterMakerProps = {
    onFilter: (filter: string, value: string) => void,
    name: string,
    options: Array<string>,
    data: Array<string>,
    onRemove: (filter: string, value: string) => void
};

export const FilterMaker = (props: FilterMakerProps) => {

    const handleEvent = (event: React.ChangeEvent<HTMLSelectElement>) => {
        if(event.target.value) {
            props.onFilter(props.name, event.target.value);
        }
    }

    return (
        <div>
            <Heading size='md' mb={4}>Select {props.name}</Heading>
            <Select
                placeholder={props.name}
                onChange={handleEvent}>
                {props.options.map(item => (
                    <option value={props.name.concat(":").concat(item)}>{item}</option>
                ))
                }
            </Select>
            <Divider my={1} />
            <HStack spacing={2}>
                {props.data.map(item => (
                <Tag
                    borderRadius='full'
                    variant='solid'
                    backgroundColor='#2196F3'
                >
                    <TagLabel>{item.split(":",2)[1]}</TagLabel>
                    <TagCloseButton onClick={() => props.onRemove(props.name, item)}/>
                </Tag>
                ))}
            </HStack>
        </div>
    );
}
