import { AddIcon } from "@chakra-ui/icons";
import {
    Text,
    HStack,
    Tag,
    TagLabel,
    TagLeftIcon,
    Table,
    TableContainer,
    Thead,
    Tr,
    Th,
    Tbody,
    Td,
    Badge,
    Wrap,
} from "@chakra-ui/react";


type PCSearchHandlerProps = {
    filters: Array<string>,
    results: Array<string>,
}


export const PCSearchHandler = (props: PCSearchHandlerProps) => {
    return (
        <div>
            <Text>Applied Filters:</Text>

            <HStack spacing={2}>
                {[...props.filters].map(item => (
                    <div>
                        <Tag
                            borderRadius='full'
                            variant='solid'
                            backgroundColor='#FFB74D'>
                            <TagLeftIcon boxSize='12px' as={AddIcon} />
                            <TagLabel>{item}</TagLabel>
                        </Tag>
                    </div>
                ))}
            </HStack>
            <TableContainer>
                <Table variant='simple' size='sm'>
                    <Thead>
                        <Tr>
                            <Th>Name</Th>
                            <Th>Institution</Th>
                            <Th>Expertise</Th>
                            <Th>Region</Th>
                            <Th>Junior</Th>
                            <Th>Female or NB</Th>
                            <Th>Industry</Th>
                        </Tr>
                    </Thead>
                    <Tbody>
                        {props.results.map(item => (
                            <Tr>
                                <Td>
                                    {item[0]}
                                </Td>
                                <Td>
                                    {item[3]}
                                </Td>
                                <Td>
                                    <Wrap display='flex'>
                                        {[...item[4].split(',')].filter(entry => entry.length > 0).map(topic => (
                                            <Badge ml='1' fontSize='0.8em' colorScheme='blue'>
                                                {topic}
                                            </Badge>
                                        ))}
                                    </Wrap>
                                </Td>
                                <Td>
                                    {item[5]}
                                </Td>
                                <Td>
                                    {item[6] !== '' && item[6] !== 'senior' && parseInt(item[6]) >= 2018 ? <Badge colorScheme="green">Yes ({item[6]})</Badge> : <Badge colorScheme="red">No</Badge>}
                                </Td>
                                <Td>
                                    {item[7] === '1' ? <Badge colorScheme="green">Yes</Badge> : <Badge colorScheme="red">No</Badge>}
                                </Td>
                                <Td>
                                    {item[9] === '1' ? <Badge colorScheme="green">Yes</Badge> : <Badge colorScheme="red">No</Badge>}
                                </Td>
                            </Tr>
                        ))}
                    </Tbody>
                </Table>
            </TableContainer>
        </div>
    );
}



