import { SearchIcon, DeleteIcon, DownloadIcon } from "@chakra-ui/icons";
import {
  Box,
  Heading,
  Divider,
  Container,
  Text,
  useBoolean,
  Button,
  Switch,
} from "@chakra-ui/react";
import { useEffect, useState } from "react";
import { FilterMaker } from "../components/FilterMaker";
import { PCSearchHandler } from "../components/PCSearchHandler";
import Papa, { ParseResult } from "papaparse";
import { render } from "react-dom";
import { useParams } from "react-router-dom";
import { useNavigate } from "react-router-dom";

interface PC {
  Name: string,
  GoogleScholar: string,
  Email: string,
  Institution: string,
  Expertise: string,
  Region: string,
  PhDGradYear: string,
  FemaleNB: string,
  Minority: string,
  Industry: string,
}

export default function Organize() {
  const [allUsers, setAllUsers] = useState(new Array<string>());
  const [isRead, setRead] = useBoolean(false);
  const [foundUsers, setFoundUsers] = useState(new Array<string>());
  const [researchKeywordList, setResearchKeywordList] = useState(new Array<string>());
  const [regionList, setRegionList] = useState(new Array<string>());
  const [allFilters, setAllFilters] = useState(new Array<string>());
  const [appliedFilters, setAppliedFilters] = useState(new Array<string>());
  const [isJunior, setJunior] = useBoolean(false);
  const [isGender, setGender] = useBoolean(false);
  const [isIndustry, setIndustry] = useBoolean(false);
  const { organizeFilters } = useParams();

  // set initial filters
  useEffect(() => {
    if (organizeFilters) {
      readData();
      addFilters(organizeFilters.split(','));
      //setFilters();
    }
  }, []);

  // adds multiple filters to list of ilters
  const addFilters = (values: string[]) => {
    values.forEach((item) => {
      const items = item.split(":");
        if(items.length === 2) {       
          const filter = items[0];
          allFilters.push(item);
          switch (filter) {
            case 'Expertise':
              if (researchKeywordList.indexOf(item) === -1) {
                researchKeywordList.push(item);
              }
              break;
            case 'Region':
              if (regionList.indexOf(item) === -1) {
                regionList.push(item);
              }
              break;
            case 'Junior':
              setJunior.on();
              break;
            case 'Gender':
              setGender.on();
              break;
            case 'Industry':
              setIndustry.on();
              break;
          }
        }
    });
    setResearchKeywordList([...researchKeywordList]);
    setRegionList([...regionList]);
    setAllFilters([...allFilters]);
    setFilters();
  }

  const navigate = useNavigate();

  const handleSearchResult = () => {
    //const t1 = performance.now();
  
    setFilters();
    navigate(`/weave-organize-demo/${[...allFilters]}`);
    /*
    setAppliedFilters([...allFilters]);
    fetchInfo(allUsers, regionList, researchKeywordList, isJunior, isGender, isIndustry);
    const t2 = performance.now();
    console.log("Time took ", t2 - t1, " ms");*/
  };

  // adds filter to list of filters
  const addFilter = (filter: string, value: string) => {
    allFilters.push(value);
    setAllFilters([...allFilters]);
    switch (filter) {
      case 'Expertise':
        if (researchKeywordList.indexOf(value) === -1) {
          researchKeywordList.push(value);
          setResearchKeywordList([...researchKeywordList]);
        }
        break;
      case 'Region':
        if (regionList.indexOf(value) === -1) {
          regionList.push(value);
          setRegionList([...regionList]);
        }
        break;
    }
  };

  // removes filter from list of filters
  const removeFilter = (filter: string, value: string) => {
    setAllFilters([...allFilters].filter(word => word !== value));
    switch (filter) {
      case 'Expertise':
        setResearchKeywordList([...researchKeywordList].filter(word => word !== value));
        break;
      case 'Region':
        setRegionList([...regionList].filter(word => word !== value));
        break;
    }
  };

  // clears and resets filters
  const clearFilters = () => {
    setResearchKeywordList(new Array<string>());
    setRegionList(new Array<string>());
    setJunior.off();
    setGender.off();
    setIndustry.off();
    setAllFilters(new Array<string>());
    setAppliedFilters(new Array<string>());
  }

  // display all of the applied filters
  const setFilters = () => {
    const t1 = performance.now();
    var filters = [...allFilters];
    if (isJunior) {
      filters.push("Junior:RecentPhD");
    }
    if (isGender) {
      filters.push("Gender:FemaleNonBinary");
    }

    if (isIndustry) {
      filters.push("Industry:Yes");
    }
    setAppliedFilters(filters);
    if(!isRead) {
      readData();
    } else {
      fetchInfo(allUsers, regionList, researchKeywordList, isJunior, isGender, isIndustry);
    }
    const t2 = performance.now();
    console.log("Time took ", t2 - t1, " ms");
  }

  // performs initial read of data
  const readData = () => {
      var csvFilePath = require("../assets/PeopleWeave_Manually_Curated_Data-SIG_Members.csv");
      fetch(csvFilePath)
        .then(response => response.text())
        .then(responseText => {
          Papa.parse<PC>(responseText, {
            complete: (data: ParseResult<PC>
            ) => {
              var formattedData = new Array<string>();
              for (let index = 1; index < data['data'].length; index++) {
                var user = JSON.parse(JSON.stringify(data['data'][index]));
                formattedData.push(user);
              }
              setAllUsers(formattedData);
              fetchInfo(formattedData, regionList, researchKeywordList, isJunior, isGender, isIndustry);
            }
          });

        });
  }

  // performs search
  const fetchInfo = (sourceData: Array<string>, regionFilters: Array<string>, researchFilters: Array<string>, junior: boolean, gender: boolean, industry: boolean) => {
    var results = new Array<string>();
    for (let index = 1; index < sourceData.length; index++) {
      var user = sourceData[index];
      if (junior && (parseInt(user[6]) < 2018 || user[6] === '' || user[6] === 'senior')) {
        continue;
      }
  
      if (gender && user[7] !== "1") {
        continue;
      }
  
      if (industry && user[9] !== '1') {
        continue;
      }
  
      if (regionFilters.length !== 0 && regionFilters.indexOf("Region:".concat(user[5])) === -1) {
        continue;
      }
  
      var found = false;
      for (let researchIndex = 0; researchIndex < researchFilters.length; researchIndex++) {
        found = user[4].indexOf(researchFilters[researchIndex].split(":")[1]) !== -1;
        if (found) {
          break;
        }
      }
  
      if (found || researchFilters.length === 0) {
        results.push(user);
      }
    }
    setFoundUsers(results);
  }

  const exportResults = () => {
    //adapted from https://stackoverflow.com/questions/54650958/how-to-download-a-csv-generated-from-papaparse
    var csv = Papa.unparse(foundUsers);
    var csvData = new Blob([csv], {type: 'text/csv;charset=utf-8;'});

    var csvURL = window.URL.createObjectURL(csvData);
    
    var testLink = document.createElement('a');
    testLink.href = csvURL;
    testLink.setAttribute('test', 'test.csv');
    testLink.click();
  }

    return (
      <Box pt={2} px={2}>
        <Container maxW="container.xl">
          <Heading mt={8}>Organize an Event</Heading>
          <Divider my={3} />
          <Text as='i'>We Recognize the community wants to improve diversity and inclusion of program committees, but knowing who is out there is difficult. Please use this search tool to discover a list of members for your program committee that take into account various constraints that you may wish to follow</Text>
          <Divider my={3} />
          <FilterMaker
            name='Expertise'
            onFilter={addFilter}
            options={['CDN', 'CLOUD-DC', 'ROUTING', 'ECONOMICS', 'MEASUREMENT', 'VERIFICATION', 'SECURITY', 'ML', 'WIRELESS', 'HARDWARE', 'PROGRAMMABLE-NW', 'SDN', 'ENERGY', 'THEORY', 'CONGESTIONCONTROL']}
            onRemove={removeFilter}
            data={researchKeywordList}
          />

          <Divider my={3} />

          <FilterMaker
            name='Region'
            onFilter={addFilter}
            options={['NA', 'Asia', 'Middle East', 'Latin America', 'Australia/NZ', 'EU']}
            onRemove={removeFilter}
            data={regionList}
          />

          <Divider my={3} />
          <Switch mr={5}
            //iconColor='blue.400'
            isChecked={isIndustry}
            onChange={setIndustry.toggle}>
            Industry
          </Switch>
          <Switch mr={5}
            //iconColor='blue.400'
            isChecked={isJunior}
            onChange={setJunior.toggle}>
            Junior (recent PhD) at most 4 years ago
          </Switch>
          <Switch mr={5}
            //iconColor='blue.400'
            isChecked={isGender}
            onChange={setGender.toggle}>
            Female or Non-binary (curated)
          </Switch>
          <Divider my={3} />
          <Button
            bg="blue.500"
            cursor="pointer"
            _hover={{
              bg: "blue.400",
              transition: "all 0.1s",
            }}
            _active={{
              bg: "blue.600",
            }}
            onClick={handleSearchResult}
            children={[<SearchIcon color="white" />, <Text ml={3}>Search for People</Text>]} />
          <Button
            ml={3}
            onClick={clearFilters}
            bg="orange.500"
            cursor="pointer"
            _hover={{
              bg: "orange.400",
              transition: "all 0.1s",
            }}
            _active={{
              bg: "orange.600",
            }}
            children={[<DeleteIcon color="white" />, <Text ml={3}>Clear Filters</Text>]} />
          <Button
            ml={3}
            onClick={exportResults}
            bg="green.500"
            cursor="pointer"
            _hover={{
              bg: "green.400",
              transition: "all 0.1s",
            }}
            _active={{
              bg: "green.600",
            }}
            children={[<DownloadIcon color="white" />, <Text ml={3}>Download Results</Text>]}
          />
          <Divider my={1} />
          <PCSearchHandler
            filters={appliedFilters}
            results={[...foundUsers]}
          />
          <Divider my={10} />
        </Container>
      </Box>
    );
}
